import { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import { api } from '../../../services/api';
import { useToast } from '../../../components/ui/ToastProvider';
import styles from '../product-sales-detail.module.css';

interface OrderItem {
  _id: string;
  orderNumber: string;
  customerName: string;
  quantity: number;
  price: number;
  total: number;
  status: 'completed' | 'cancelled';
  date: string;
}

interface TrendData {
  date: string;
  unitsSold: number;
  revenue: number;
}

interface ProductDetail {
  productId: string;
  productName: string;
  category: string;
  currentStock: number;
  totalUnitsSold: number;
  totalRevenue: number;
  avgSellingPrice: number;
  ordersCount: number;
  completedOrdersCount: number;
  cancelledOrdersCount: number;
  cancellationRate: number;
  stockSalesRatio: number;
  trendData: TrendData[];
  orders: OrderItem[];
}

export default function ProductSalesDetailPage() {
  const router = useRouter();
  const { productId } = router.query;
  const { pushToast } = useToast();
  
  const [vendorId, setVendorId] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [detail, setDetail] = useState<ProductDetail | null>(null);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');

  useEffect(() => {
    if (typeof window !== 'undefined') {
      try {
        const stored = localStorage.getItem('cc_user');
        if (stored) {
          const parsed = JSON.parse(stored);
          setVendorId(parsed.id);
        }
      } catch {}
    }
  }, []);

  useEffect(() => {
    if (vendorId && productId) {
      loadDetail();
    }
  }, [vendorId, productId, startDate, endDate]);

  const loadDetail = async () => {
    try {
      setLoading(true);
      setError(null);
      const data = await api.productSales.getProductDetail(
        productId as string,
        vendorId,
        { startDate, endDate }
      );
      setDetail(data);
    } catch (err: any) {
      setError(err.message || 'Failed to load product details');
      pushToast({ type: 'error', title: 'Load Failed', message: err.message });
    } finally {
      setLoading(false);
    }
  };

  const goBack = () => {
    router.push('/vendor/product-sales');
  };

  if (loading) {
    return (
      <div className={styles['product-detail-page']}>
        <div className={styles['pd-header']}>
          <div className={styles['pd-skeleton']} style={{ height: '80px', width: '100%' }} />
        </div>
        <div className={styles['pd-summary-grid']}>
          {[1, 2, 3, 4].map((i) => (
            <div key={i} className={styles['pd-skeleton']} style={{ height: '100px' }} />
          ))}
        </div>
      </div>
    );
  }

  if (error || !detail) {
    return (
      <div className={styles['product-detail-page']}>
        <div className={styles['pd-error-state']}>
          <h3>Error Loading Product Details</h3>
          <p>{error || 'Product not found'}</p>
          <button className={styles['pd-retry-button']} onClick={loadDetail}>
            Retry
          </button>
          <button className={styles['pd-back-button']} onClick={goBack}>
            Back to Product Sales
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className={styles['product-detail-page']}>
      <div className={styles['pd-header']}>
        <div className={styles['pd-header-left']}>
          <div className={styles['pd-breadcrumb']}>
            <span className={styles['pd-breadcrumb-link']} onClick={goBack}>
              Product Sales
            </span>
            <span>›</span>
            <span>{detail.productName}</span>
          </div>
          <h1 className={styles['pd-product-title']}>{detail.productName}</h1>
          <div className={styles['pd-product-meta']}>
            <span className={styles['pd-category-badge']}>{detail.category || 'Uncategorized'}</span>
            <span className={styles['pd-meta-separator']}>•</span>
            <span className={styles['pd-stock-info']}>Stock: {detail.currentStock} units</span>
          </div>
        </div>
        <div className={styles['pd-date-selector']}>
          <label>From:</label>
          <input
            type="date"
            className={styles['pd-date-input']}
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
          />
          <label>To:</label>
          <input
            type="date"
            className={styles['pd-date-input']}
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
          />
        </div>
      </div>

      <div className={styles['pd-summary-grid']}>
        <div className={styles['pd-summary-card']}>
          <div className={styles['pd-summary-label']}>Total Units Sold</div>
          <div className={styles['pd-summary-value']}>{detail.totalUnitsSold.toLocaleString()}</div>
        </div>
        <div className={styles['pd-summary-card']}>
          <div className={styles['pd-summary-label']}>Total Revenue</div>
          <div className={styles['pd-summary-value']}>
            ₹{detail.totalRevenue.toLocaleString(undefined, { minimumFractionDigits: 2 })}
          </div>
        </div>
        <div className={styles['pd-summary-card']}>
          <div className={styles['pd-summary-label']}>Avg Selling Price</div>
          <div className={styles['pd-summary-value']}>₹{detail.avgSellingPrice.toFixed(2)}</div>
        </div>
        <div className={styles['pd-summary-card']}>
          <div className={styles['pd-summary-label']}>Orders Count</div>
          <div className={styles['pd-summary-value']}>{detail.ordersCount}</div>
        </div>
      </div>

      <div className={styles['pd-trends-section']}>
        <h2 className={styles['pd-section-title']}>Sales Trends</h2>
        <div className={styles['pd-chart-container']}>
          {detail.trendData.length === 0 ? (
            <div className={styles['pd-empty-chart']}>
              <p>No trend data available for the selected period</p>
            </div>
          ) : (
            <div className={styles['pd-simple-chart']}>
              <div className={styles['pd-chart-legend']}>
                <div className={styles['pd-legend-item']}>
                  <span className={styles['pd-legend-color']} style={{ background: 'var(--primary)' }} />
                  <span>Units Sold</span>
                </div>
                <div className={styles['pd-legend-item']}>
                  <span className={styles['pd-legend-color']} style={{ background: 'var(--secondary)' }} />
                  <span>Revenue (₹)</span>
                </div>
              </div>
              <div className={styles['pd-chart-bars']}>
                {detail.trendData.map((trend, idx) => {
                  const maxUnits = Math.max(...detail.trendData.map((t) => t.unitsSold), 1);
                  const maxRevenue = Math.max(...detail.trendData.map((t) => t.revenue), 1);
                  const unitsHeight = (trend.unitsSold / maxUnits) * 100;
                  const revenueHeight = (trend.revenue / maxRevenue) * 100;
                  
                  return (
                    <div key={idx} className={styles['pd-chart-bar-group']}>
                      <div className={styles['pd-chart-bars-container']}>
                        <div
                          className={styles['pd-chart-bar']}
                          style={{
                            height: `${unitsHeight}%`,
                            background: 'var(--primary)',
                          }}
                          title={`Units: ${trend.unitsSold}`}
                        />
                        <div
                          className={styles['pd-chart-bar']}
                          style={{
                            height: `${revenueHeight}%`,
                            background: 'var(--secondary)',
                          }}
                          title={`Revenue: ₹${trend.revenue.toFixed(2)}`}
                        />
                      </div>
                      <div className={styles['pd-chart-label']}>
                        {new Date(trend.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          )}
        </div>
      </div>

      <div className={styles['pd-stats-row']}>
        <div className={styles['pd-stat-card']}>
          <div className={styles['pd-stat-label']}>Completed Orders</div>
          <div className={styles['pd-stat-value']}>{detail.completedOrdersCount}</div>
        </div>
        <div className={styles['pd-stat-card']}>
          <div className={styles['pd-stat-label']}>Cancelled Orders</div>
          <div className={styles['pd-stat-value']} style={{ color: 'var(--error)' }}>
            {detail.cancelledOrdersCount}
          </div>
        </div>
        <div className={styles['pd-stat-card']}>
          <div className={styles['pd-stat-label']}>Cancellation Rate</div>
          <div className={styles['pd-stat-value']} style={{ color: detail.cancellationRate > 10 ? 'var(--error)' : 'var(--text-primary)' }}>
            {detail.cancellationRate.toFixed(1)}%
          </div>
        </div>
        <div className={styles['pd-stat-card']}>
          <div className={styles['pd-stat-label']}>Stock vs Sales Ratio</div>
          <div className={styles['pd-stat-value']}>
            {detail.stockSalesRatio.toFixed(2)}
          </div>
          <div className={styles['pd-stat-subtext']}>
            {detail.stockSalesRatio < 1 ? 'Low stock' : detail.stockSalesRatio < 3 ? 'Moderate' : 'High stock'}
          </div>
        </div>
      </div>

      <div className={styles['pd-orders-section']}>
        <h2 className={styles['pd-section-title']}>Order History</h2>
        <div className={styles['pd-table-wrapper']}>
          {detail.orders.length === 0 ? (
            <div className={styles['pd-empty-state']}>
              <p>No orders found for this product in the selected period</p>
            </div>
          ) : (
            <table className={styles['pd-table']}>
              <thead className={styles['pd-table-head']}>
                <tr>
                  <th>Order Number</th>
                  <th>Customer</th>
                  <th>Quantity</th>
                  <th>Price</th>
                  <th>Total</th>
                  <th>Status</th>
                  <th>Date</th>
                </tr>
              </thead>
              <tbody className={styles['pd-table-body']}>
                {detail.orders.map((order) => (
                  <tr key={order._id}>
                    <td className={styles['pd-order-number']}>{order.orderNumber}</td>
                    <td>{order.customerName || '—'}</td>
                    <td>{order.quantity}</td>
                    <td>₹{order.price.toFixed(2)}</td>
                    <td>₹{order.total.toFixed(2)}</td>
                    <td>
                      <span
                        className={`${styles['pd-status-badge']} ${
                          order.status === 'completed' ? styles['status-completed'] : styles['status-cancelled']
                        }`}
                      >
                        {order.status}
                      </span>
                    </td>
                    <td>{new Date(order.date).toLocaleDateString()}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
}
